import React from 'react';
import styled from 'styled-components';
import Vimeo from '@u-wave/react-vimeo';
import Fade from 'react-reveal/Fade';
import Img from 'gatsby-image';
import { Container } from '@components/global';

const ImgSingle = ({ header, text, video, img, caption, toggletop, togglecover }) => (
  
  <Container>
    <HeaderWrapper>
      <ToggleTopText toggletop={toggletop}>
        <Grid>
          <div/>
          <Fade>
          <TopTextMargin>
            <h3>{header}</h3>
            <p>{text}</p>
          </TopTextMargin>
          </Fade>
        </Grid>
      </ToggleTopText>
      <Art>
        <ToggleCoverText togglecover={togglecover}>
          <CoverText>
            <Grid>
              <div/>
              <CoverTextMargin>
                <Fade>
                  <h3>{header}</h3>
                  <p>{text}</p>
                </Fade>
              </CoverTextMargin>
            </Grid>
          </CoverText>
        </ToggleCoverText>
        <Vimeo
          video={video}
          width="100%"
          responsive="true"
          autoplay
        />
        <ImgCovered>
          <img src={img}/>
        </ImgCovered>
        <Caption>{caption}</Caption>
      </Art>
    </HeaderWrapper>
  </Container>
);


const HeaderWrapper = styled.div`
  padding: 50px 0 50px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }
  ${props =>
    props.togglecover &&
    `padding: ${
      props.togglecover === 'false'
        ? '50px 0 50px 0;'
        : '0px 0px 0px 0px;'
    }`};
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;

const ToggleTopText = styled.div`
  display: none;
  ${props =>
    props.toggletop &&
    `display: ${
      props.toggletop === 'false'
        ? 'none'
        : 'block'
    }`};
  }
`;

const TopTextMargin = styled.div`
  margin: 0 30% 50px 0;
  @media (max-width: ${props => props.theme.screen.md}) {
    margin: 0;
  }
`;

const ToggleCoverText = styled.div`
  display: none;
  ${props =>
    props.togglecover &&
    `display: ${
      props.togglecover === 'false'
        ? 'none'
        : 'block'
    }`};
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  left: 0;
  img{
    width: 100%;
  }
`;

const CoverText = styled.div`
  position: relative;
  z-index: 2;
  top: -50px;
  left: 50px;
  height: 0;
  color:  ${props => props.theme.color.grayscale.S900};
  @media (max-width: ${props => props.theme.screen.md}) {
    z-index: 1;
    top: 0px;
    left: 0px;
    height: auto;
    color:  ${props => props.theme.color.grayscale.S1000};
  }
`;

const CoverTextMargin = styled.div`
  margin: 0 0 0 50%;
  padding: 20px 35px 45px 35px;
  background-color:  ${props => props.theme.color.white.regular};
  @media (max-width: ${props => props.theme.screen.md}) {
    margin: 0;
    background-color:  transparent;
    padding: 0;
  }
`;

const ImgCovered = styled.div`
  position: relative;
  z-index: 1;
  left: -50px;
  width: calc(100% + 100px);
`;

const Caption = styled.div`
  padding-top:5px;
  padding-right: 30%;
  ${props => props.theme.font_size.xsmall};
`;


export default ImgSingle;
